<template>
  <div>
    <el-card shadow='hover' class='card'>
      <div class='type item'>
        <span class='left'>{{$t('Admin.WorkType')}}</span>
        <span class='right'>{{$t('Admin.WorkAuth')}}</span>
      </div>
      <div class='from item'>
        <span class='left'>{{$t('Admin.WorkFrom')}}</span>
        <span class='right'>{{realname}} ({{userid}})</span>
      </div>

      <div class='target item'>
        <span class='left'>{{$t('Admin.AuthType')}}</span>
        <span class='right'>{{authtypeFormat}}</span>
      </div>
      <div class='reason item'>
        <span class='left'>{{$t('Admin.AuthName')}}</span>
        <span class='right'>{{authname}}</span>
      </div>

      <div class='button'>
        <el-button type='primary' @click='failAuth'>{{$t('Admin.AuthFail')}}</el-button>
        <el-button type='danger' @click='successAuth'>{{$t('Admin.AuthSuccess')}}</el-button>
      </div>

    </el-card>
  </div>
</template>

<script>
export default {
  name: 'admin-auth',
  props: {
    workid: { type: Number },
    type: { type: Number },
    userid: { type: Number },
    realname: { type: String },
    authtype: { type: Number },
    authname: { type: String },
    time: { type: String },
  },
  computed: {
    authtypeFormat () {
      return this.$t('User.AuthType' + this.authtype);
    }
  },
  methods: {
    failAuth () {
      this.$bus.$emit('completeAdminWork', this.workid, 0);
    },
    successAuth () {
      this.$bus.$emit('completeAdminWork', this.workid, 1);
    },
  }
}
</script>

<style scoped>
.card {
  border-radius: 10px;
  border: 1px solid red;
  text-align: left;
  font-size: 20px;
}
.right {
  float: right;
}
.item {
  margin-top: 10px;
}
.button {
  margin-top: 30px;
  text-align: center;
}
</style>